	max-width: 150px !important;
}

#home-latest-transaction-sizing > div > .rt-thead > div > div:nth-child(1), #home-latest-transaction-sizing > div > .rt-tbody > div > div > div:nth-child(1) {
	min-width: 70px !important;
	max-width: 70px !important;
} 

#home-latest-transaction-sizing > div > .rt-thead > div > div:nth-child(2), #home-latest-transaction-sizing > div > .rt-tbody > div > div > div:nth-child(2) {
