} 

#home-latest-transaction-sizing > div > .rt-thead > div > div:nth-child(3), #home-latest-transaction-sizing > div > .rt-tbody > div > div > div:nth-child(3) {
	min-width: 45px !important;
	max-width: calc((100% - 330px) / 2);
} 

#home-latest-transaction-sizing > div > .rt-thead > div > div:nth-child(4), #home-latest-transaction-sizing > div > .rt-tbody > div > div > div:nth-child(4) {
