.layout > div {
	display: inline-block;
	width: 100%;
	text-align: center;
}

.layout > div > div {
	display: inline-block;
	text-align: left;
}

.topheader {
	height: 40px;
	background-color: var(--bg-dark);
	position: fixed;
	z-index: 9999;
	max-width: 100vw;
}

.bottomheader {
	height: 65px;
	padding-top: 40px;
	background-color: #fefefe;
	box-shadow: 0 2px 10px rgb(151, 164, 175, .1);
	z-index: 1000;
	position: relative;
}

.content {
	min-height: calc(100vh - 185px);
	background-color: #fff;
	position: relative;
}

.content-shortened {
	min-height: calc(100vh - 235px);
}

.footer {
	height: 80px;
	background-color: var(--bg-dark);
}

.subfooter {
	background-color: #fafcff;
	padding: 64px 0px;
	border-top: 1px solid #e7eaf3;
}

.scrolltop {
	position: fixed;
	width: 40px !important;
	height: 40px !important;
	bottom: 15px;
	right: 15px;
	border-radius: 4px;
	background-color: #337afe;
	opacity: 0.5;
	z-index: 9999;
	display: inline-block;
	transition: 200ms ease-in-out;
	color: #fff;
	border: none;
	cursor: pointer;
	font-size: 15px;
	transform: rotate(-90deg);
}

.scrolltop:hover {
	opacity: 1;
}

.scrolltop:focus {
	outline: none;
}

.hiddenscroll {
	opacity: 0;
	visibility: hidden;
}

@media screen and (min-width: 1300px) {
	.sizer {
		width: 1200px;
	}
}

@media screen and (min-width: 600px) and (max-width: 1300px) {
	.sizer {
		width: 90vw;
	}
}

@media screen and (max-width: 600px) {
	.sizer {
		width: 95vw;
	}
}

.algo-icon {
	height: 14px;
	display: inline-block;
	transform: translate(0px, 1.25px);
}

.address-header {
	background-color: #fafcff;
	padding-bottom: 30px;
	border-bottom: 1px solid #e7eaf3;
}

.address-header > div > div > div:first-child {
	display: inline-block;
	min-width: 600px;
	vertical-align: top;
	height: 100px;
}

.address-header > div > div > div:last-child {
