}

.address-button > svg {
	vertical-align: middle;
}

.address-button:hover {
	background-color: #536d93;
	box-shadow: 0 4px 11px rgba(105,132,170,.35);
}

.address-button:hover svg > path {
	fill: #fff !important;
}

.qr-style {
	padding: 5px 5px 2.5px 5px !important;
	margin-top: 7px !important;
}

.copy-style {
	margin-top: -3px !important;
	z-index: 2000;
}

