	color: #fff;
	box-shadow: 0 4px 11px rgba(51,122,254,.35);
}

.menu-mobile > nav > ul > li > a:hover {
	color: #337afe;
}

.hidden {
	display: none !important;
	opacity: 1;
}

.shown {
	display: inline-block !important;
}

@media screen and (max-width: 950px) {
	.menu > nav {
		display: none;
